@import "../../../src/utils/style/libs/mixins.scss";
@import "../../../src/utils/style/libs/variable.scss";

.entrepriseFamille {
  z-index: 0;
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background-color: white;
  background-size: cover;
  .titleEntrepriseFamille {
    opacity: 0;
    margin: 85px 0 15px 0;
    position: relative;
    height: 4vh;
    z-index: 10;
    font-family: "Archivo Black";
    font-size: 35px;
    color: $primaryDark;
  }
  .entreprise {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 50%;
    height: fit-content;
    margin-bottom: 50px;
    .subtitleEntreprise {
      opacity: 0;
      z-index: 10;
      width: 100%;
      position: relative;
      font-family: "Nunito Sans";
      font-weight: bold;
      color: $primary;
      margin: 8% 0 0 7%;
      font-size: 27px;
    }
    .entrepriseChoice {
      width: 100%;
      z-index: 10;
      position: relative;
      width: 646px;
      height: 474px;
    }
    .reserverEntreprise {
      opacity: 0;
      z-index: 10;
      position: relative;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: "Nunito Sans";
      text-decoration: none;
      color: white;
      font-size: 27px;
      width: 160px;
      height: 61px;
      background: #005f4e;
      border-radius: 11px;
    }
  }
  .famille {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 50%;
    height: fit-content;
    .subtitleFamille {
      opacity: 0;
      z-index: 10;
      width: 100%;
      position: relative;
      font-family: "Nunito Sans";
      font-weight: bold;
      color: $primary;
      margin: 8% 0 0 7%;
      font-size: 27px;
    }
    .familleChoice {
      width: 100%;
      z-index: 10;
      position: relative;
      width: 646px;
      height: 474px;
    }
    .reserverFamille {
      z-index: 10;
      width: 100%;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: "Nunito Sans";
      text-decoration: none;
      color: white;
      font-size: 27px;
      width: 160px;
      height: 61px;
      background: #005f4e;
      border-radius: 11px;
    }
  }

  .frise1,
  .frise3,
  .frise5 {
    right: -1500px;
  }
  .frise2,
  .frise4,
  .frise6 {
    left: -1500px;
  }
  .frise1,
  .frise2,
  .frise3,
  .frise4,
  .frise5,
  .frise6 {
    overflow: hidden;
    z-index: 1;
    position: absolute;
    margin: 4% 0 0 0;
    width: 200%;
    opacity: 0.1;
    filter: drop-shadow(5px 5px 5px #0000008e);
    transition: 1s;
  }
  .frise2 {
    margin: 10% 0 0 0;
  }
  .frise3 {
    margin: 18% 0 0 0;
  }
  .frise4 {
    margin: 26% 0 0 0;
  }
  .frise5 {
    margin: 34% 0 0 0;
  }
  .frise6 {
    margin: 42% 0 0 0;
  }
}

// ----------RESPONSIVE----------

