@font-face {
  font-family: "Nunito Sans";
  src: url(../../../public/fonts/NunitoSans.ttf);
}
@font-face {
  font-family: "Archivo Black";
  src: url(../../../public/fonts/ArchivoBlack-Regular.ttf);
}

.imaginezVous {
  z-index: 21;
  position: relative;
  height: 900px;
  overflow-y: hidden;
  overflow-x: hidden;
  .background {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 95%;
    clip-path: polygon(0 5%, 100% 0, 100% 95%, 0 100%);
  }
  .vaisseau {
    position: absolute;
    left: 57%;
    margin-top: -220px;
    z-index: 20;
    animation: vaisseau ease-in-out infinite 50s;
    transform-origin: 312px, 312px;
  }
  .vaisseau2 {
    position: absolute;
    left: 50%;
    margin-top: 60px;
    z-index: 4;
    animation: vaisseau2 ease-in-out infinite 50s;
    transform-origin: 312px, 312px; /* Centre de l'élément */
  }
  .vaisseau3 {
    position: absolute;
    left: 80%;
    margin-top: 650px;
    z-index: 4;
    animation-delay: 2s;
    animation: vaisseau2 ease-in-out infinite 50s;
    transform-origin: 312px, 312px; /* Centre de l'élément */
  }
  .planet {
    position: absolute;
    left: 47%;
    margin-top: -70px;
    z-index: 3;
    animation: planete infinite 500s;
    transform-origin: center; /* Centre de l'élément */
    transition: 1000s;
  }
  .mustafar {
    z-index: 2;
    position: absolute;
    height: 800px;
    left: 43%;
    margin-top: -500px;
    animation: mustafar infinite 350s;
    transform-origin: 438px 438px; /* Centre de l'élément */
    transform-origin: 250px 250px;
  }
  .title {
    font-family: "Archivo Black";
    font-size: 35px;
    margin: 60px 0 0 30px;
    left: 2000px;
    opacity: 0;
    color: white;
    position: absolute;
    z-index: 21;
    transition: 1s;
  }
  .text1,
  .text2,
  .text3 {
    font-family: "Nunito Sans";
    font-weight: bold;
    font-size: 20px;
    line-height: 1.5;
    left: 2000px;
    opacity: 0;
    color: white;
    position: absolute;
    margin: 150px 0 0 30px;
    z-index: 21;
    transition: 1s;
  }
  .text2 {
    margin-top: 290px;
  }
  .text3 {
    margin-top: 465px;
  }
}

@keyframes vaisseau {
  0% {
    rotate: 0deg;
  }

  10% {
    rotate: 5deg;
  }

  20% {
    rotate: -5deg;
  }

  30% {
    rotate: 0deg;
  }

  40% {
    rotate: 5deg;
  }

  50% {
    rotate: -5deg;
  }

  60% {
    rotate: 0deg;
  }

  70% {
    rotate: 5deg;
  }

  80% {
    rotate: -5deg;
  }

  100% {
    rotate: 0deg;
  }
}

@keyframes vaisseau2 {
  0% {
    rotate: 0deg;
  }

  10% {
    rotate: -10deg;
  }

  20% {
    rotate: 10deg;
  }

  30% {
    rotate: 0deg;
  }

  40% {
    rotate: -10deg;
  }

  50% {
    rotate: 10deg;
  }

  60% {
    rotate: 0deg;
  }

  70% {
    rotate: -10deg;
  }

  80% {
    rotate: 10deg;
  }

  100% {
    rotate: 0deg;
  }
}
@keyframes planete {
  0% {
    rotate: 0deg;
  }
  100% {
    rotate: 360deg;
  }
}
@keyframes mustafar {
  0% {
    rotate: 0deg;
  }
  100% {
    rotate: -360deg;
  }
}

// ------------RESPONSIVE------------

@media screen and (max-width: 576px) {
  .imaginezVous {
    position: relative;
    height: 1000px;
    .background {
      height: 1000px !important;
      width: 1500px !important;
    }
    .vaisseau {
      left: 0% !important;
      margin-top: -320px !important;
    }
    .vaisseau2 {
      left: 50% !important;
      margin-top: 0px !important;
    }
    .vaisseau3 {
      left: 40% !important;
      margin-top: 630px !important;
    }
    .planet {
      left: 20% !important;
      margin-top: -40px !important;
    }
    .mustafar {
      height: 800px !important;
      left: -200px !important;
      margin-top: -500px !important;
    }
    .title {
      font-size: 32px;
      margin: 20px 0 0 30px;
      left: 2000px;
    }
    .text1,
    .text2,
    .text3 {
      font-size: 20px;
      line-height: 1.3;
      left: 2000px;
      position: absolute;
      margin: 80px 0 0 30px;
      filter: drop-shadow(-8px -8px 13px rgba(0, 0, 0, 0.86))
        drop-shadow(4px 4px 8px rgba(0, 0, 0, 0.29));
    }
    .text2 {
      margin-top: 330px;
    }
    .text3 {
      margin-top: 610px;
    }
  }
}

@media screen and (max-width: 768px) {
  .imaginezVous {
    .background {
      width: 230%;
    }
    .vaisseau {
      left: 15% !important;
    }
    .vaisseau2 {
      left: 50%;
    }
    .vaisseau3 {
      left: 67%;
    }
    .planet {
      left: 20%;
      margin-top: -40px;
    }
    .mustafar {
      height: 800px;
      left: -200px;
      margin-top: -500px;
    }
  }
}
