@import "../../../src/utils/style/libs/mixins.scss";
@import "../../../src/utils/style/libs/variable.scss";

@font-face {
  font-family: "Nunito Sans";
  src: url(../../../public/fonts/NunitoSans.ttf);
}
@font-face {
  font-family: "Archivo Black";
  src: url(../../../public/fonts/ArchivoBlack-Regular.ttf);
}
.hands {
  z-index: 20;
  position: absolute;
  height: 200px;
  width: 200px;
  left: calc(50% - 100px);
  top: calc(50% - 100px);
}
.line {
  z-index: 10;
  position: relative;
  background-color: $primary;
  height: 3px;
  width: 100%;
  opacity: 0.5;
  clip-path: polygon(50% 0, 50% 0, 50% 100%, 50% 100%);
  animation: line 3s forwards;
  animation-delay: 5s;
}
.sectionUsers {
  z-index: 8;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: fit-content;
  .user1div {
    position: relative;
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    padding: 1% 0 1% 0;
    background-color: rgba(208, 237, 255, 0.485);
    clip-path: polygon(0 0, 84% 0, 100% 100%, 0% 100%);
    .user1 {
      margin: 0 0 1% 3%;
      height: 150px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      .imageUser {
        opacity: 0;
        height: 100%;
        border-radius: 50%;
      }
      .informationsUser {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 68%;
      }
      .commentaireUser {
        padding-top: 20px;
        width: 80%;
        height: 50%;
        line-height: 26px;
        font-family: "Nunito Sans";
        font-size: 17px;
        font-style: italic;
      }
      .nameUser {
        width: 100%;
        height: 10%;
        font-family: "Archivo Black";
        font-size: 17px;
      }
      .entrepriseUser {
        width: 35%;
      }
    }
  }
  .user2div {
    position: relative;
    width: 50%;
    padding: 1% 0 1% 0;
    background-color: rgba(255, 232, 243, 0.582);
    clip-path: polygon(16% 0, 100% 0, 100% 100%, 0 100%);
    .user2 {
      margin: 0 0 1% 17%;
      height: 150px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .imageUser2 {
        opacity: 0;
        height: 100%;
        border-radius: 50%;
      }
      .informationsUser2 {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 68%;
      }
      .commentaireUser2 {
        padding-top: 20px;
        width: 90%;
        height: 50%;
        line-height: 26px;
        font-family: "Nunito Sans";
        font-size: 17px;
        font-style: italic;
      }
      .nameUser2 {
        margin-top: 22px;
        width: 100%;
        height: 10%;
        font-family: "Archivo Black";
        font-size: 17px;
      }
      .entrepriseUser2 {
        width: 35%;
      }
    }
  }
  .user3div {
    position: relative;
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    padding: 1% 0 1% 0;
    background-color: rgba(231, 255, 247, 0.62);
    clip-path: polygon(0 0, 100% 0, 84% 100%, 0 100%);
    .user3 {
      margin: 0 0 1% 3%;
      height: 150px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .imageUser3 {
        opacity: 0;
        height: 100%;
        border-radius: 50%;
      }
      .informationsUser3 {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 68%;
      }
      .commentaireUser3 {
        padding-top: 20px;
        width: 90%;
        height: 50%;
        line-height: 26px;
        font-family: "Nunito Sans";
        font-size: 17px;
        font-style: italic;
      }
      .nameUser3 {
        width: 100%;
        height: 20%;
        font-family: "Archivo Black";
        font-size: 17px;
      }
      .entrepriseUser3 {
        width: 35%;
      }
    }
  }
  .user4div {
    position: relative;
    width: 50%;
    padding: 1% 0 1% 0;
    background-color: rgba(235, 237, 255, 0.608);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 16% 100%);
    .user4 {
      margin: 0 0 1% 17%;
      height: 150px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .imageUser4 {
        opacity: 0;
        height: 100%;
        border-radius: 50%;
      }
      .informationsUser4 {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 68%;
      }
      .commentaireUser4 {
        padding-top: 20px;
        width: 90%;
        height: 50%;
        line-height: 26px;
        font-family: "Nunito Sans";
        font-size: 17px;
        font-style: italic;
      }
      .nameUser4 {
        width: 100%;
        height: 10%;
        font-family: "Archivo Black";
        font-size: 17px;
      }
      .entrepriseUser4 {
        width: 35%;
      }
    }
  }
}

@keyframes line {
  from {
    clip-path: polygon(50% 0, 50% 0, 50% 100%, 50% 100%);
  }

  to {
    clip-path: polygon(100% 0, 0 0, 0 100%, 100% 100%);
  }
}

// -----------RESPONSIVE------------

@media screen and (max-width: 567px) {
  .hands {
    display: none;
  }
  .sectionUsers {
    position: relative !important;
    display: flex !important;
    flex-wrap: wrap !important;
    width: 100% !important;
    height: fit-content !important;
    .user1div {
      width: 96% !important;
      height: 210px !important;
      display: flex !important;
      // flex-wrap: wrap;
      padding: 1% 0 1% 0 !important;
      background-color: rgb(255, 248, 208) !important;
      clip-path: polygon(0 0, 84% 0, 100% 100%, 0% 100%) !important;
      .user1 {
        margin: 0 0 1% 0% !important;
        width: 92% !important;
        height: 175px !important;
        .imageUser {
          height: 62% !important;
          margin: auto !important;
        }
        .informationsUser {
          width: 100% !important;
        }
        .commentaireUser {
          padding-top: 0px !important;
          width: 92% !important;
          height: 50% !important;
          line-height: 20px !important;
          font-size: 19px !important;
          margin: auto !important;
        }
        .nameUser {
          width: 57% !important;
          padding-top: 5px !important;
          padding-bottom: 10px !important;
          font-size: 18px !important;
          margin: auto !important;
        }
        .entrepriseUser {
          padding-top: 10px !important;
          width: 35% !important;
        }
      }
    }
    .user2div {
      position: relative !important;
      left: 4% !important;
      width: 96% !important;
      height: 210px !important;
      display: flex !important;
      padding: 1% 0 1% 0 !important;

      .user2 {
        position: relative !important;
        margin: 0 0 1% 6% !important;
        width: 92% !important;
        height: 175px !important;
        .imageUser2 {
          left: 4% !important;
          height: 62% !important;
          margin: auto !important;
        }
        .informationsUser2 {
          position: relative !important;
          width: 100% !important;
          .commentaireUser2 {
            padding-top: 0px !important;
            width: 92% !important;
            height: 50% !important;
            line-height: 20px !important;
            font-size: 18px !important;
            margin: auto !important;
          }
          .nameUser2 {
            margin-top: 0px !important;
            left: 80% !important;
            width: 57% !important;
            padding-top: 5px !important;
            padding-bottom: 10px !important;
            font-size: 18px !important;
            margin-left: 65% !important;
          }
          .entrepriseUser2 {
            padding-top: 10px !important;
            width: 35% !important;
          }
        }
      }
    }
    .user3div,
    .user4div {
      display: none !important;
    }
  }
}

@media screen and (max-width: 768px) {
  .hands {
    display: none;
  }
  .line {
    height: 3px;
    width: 100%;
  }
  .sectionUsers {
    .user1div {
      width: 90%;
      .user1 {
        align-items: center;
        .informationsUser {
          width: 70%;
        }
        .nameUser {
          margin-top: 20px;
        }
      }
    }
    .user2div {
      left: 10%;
      width: 90%;
      .user2 {
        align-items: center;
        .informationsUser2 {
          width: 70%;
        }
        .nameUser2 {
          margin-top: 20px;
        }
      }
    }
    .user3div {
      width: 90%;
      clip-path: polygon(0 0, 84% 0, 100% 100%, 0% 100%);
      .user3 {
        align-items: center;
        .informationsUser3 {
          width: 70%;
        }
        .nameUser3 {
          margin-top: 20px;
        }
      }
    }
    .user4div {
      left: 10%;
      width: 90%;
      .user4 {
        align-items: center;
        .informationsUser4 {
          width: 70%;
        }
        .nameUser4 {
          margin-top: 20px;
        }
      }
    }
  }
}
