.reassuranceBrands {
  overflow-y: hidden;
  overflow-x: hidden;
  z-index: 9;
  position: absolute;
  height: 120px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  margin-top: -60px;
  filter: drop-shadow(0px 11px 20px rgba(0, 0, 0, 0.25));
  clip-path: polygon(0 35%, 100% 0%, 100% 65%, 0% 100%);
  .nosPartenaires {
    font-family: "Archivo Black";
    font-size: 14px;
    color: rgba(0, 0, 0, 0.45);
  }
  .divLogoPartenaires {
    position: relative;
    .logoPartenaires {
      width: 125px;
      opacity: 55%;
    }
  }
}

@media screen and (max-width: 576px) {
  .nosPartenaires {
    display: none !important;
  }
  .logoPartenaires {
    width: 105px !important;
    opacity: 72% !important;
  }
}
