@import "../../../src/utils/style/libs/mixins.scss";
@import "../../../src/utils/style/libs/variable.scss";

.entrepriseFamille {
  overflow-y: hidden;
  overflow-x: hidden;
  z-index: 5;
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  height: fit-content;
  background-color: white;
  .title {
    margin: 65px 0 15px 0;
    position: relative;
    height: 4vh;
    z-index: 10;
    font-family: "Archivo Black";
    font-size: 47px;
    color: $primaryDark;
  }
  .entreprise1 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 50%;
    height: fit-content;
    margin-bottom: 50px;
    .subtitleEntreprise {
      z-index: 10;
      width: 100%;
      position: relative;
      font-family: "Nunito Sans";
      font-weight: bold;
      color: $primary;
      margin: 8% 0 0 7%;
      font-size: 33px;
    }
    .entrepriseChoice {
      width: 100%;
      z-index: 10;
      position: relative;
      width: 646px;
      height: 474px;
    }
    .reserverEntreprise {
      z-index: 10;
      position: relative;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: "Nunito Sans";
      text-decoration: none;
      color: white;
      font-size: 30px;
      width: 160px;
      height: 61px;
      background: #005f4e;
      //   box-shadow: inset -2px -2px 4px rgba(0, 0, 0, 0.22),
      //     inset 1px 2px 3px rgba(255, 255, 255, 0.34);
      //   filter: drop-shadow(-8px -8px 13px rgba(255, 255, 255, 0.86))
      //     drop-shadow(4px 4px 15px rgba(0, 0, 0, 0.29));
      border-radius: 11px;
    }
  }

  .frise15,
  .frise17,
  .frise19 {
    right: -1500px;
  }
  .frise16,
  .frise18,
  .frise20 {
    left: -1500px;
  }
  .frise15,
  .frise16,
  .frise17,
  .frise18,
  .frise19,
  .frise20 {
    overflow: hidden;
    z-index: 1;
    position: absolute;
    margin: 4% 0 0 0;
    width: 200%;
    opacity: 0.1;
    filter: drop-shadow(5px 5px 5px #0000008e);
    transition: 1s;
  }
  .frise16 {
    margin: 10% 0 0 0;
  }
  .frise17 {
    margin: 18% 0 0 0;
  }
  .frise18 {
    margin: 26% 0 0 0;
  }
  .frise19 {
    margin: 34% 0 0 0;
  }
  .frise20 {
    margin: 42% 0 0 0;
  }
}

// ----------RESPONSIVE----------

@media screen and (min-width: 1200px) {
}

@media screen and (max-width: 576px) {
  .entrepriseFamille {
    z-index: 0;
    position: relative;
    height: fit-content !important;
    background-color: white;
    .title {
      padding-top: 66px;
      position: relative;
      text-align: center;
      margin: auto;
      font-size: 23px !important;
    }
    .entreprise1 {
      width: 100%;
      height: fit-content;
      margin-top: 20px;
      .subtitleEntreprise {
        text-align: center;
        color: $primary;
        font-size: 26px !important;
      }
      .entrepriseChoice {
        width: 367px !important;
        height: 290px !important;
      }
    }
  }
  .frise15,
  .frise17,
  .frise19 {
    right: -1500px;
  }
  .frise16,
  .frise18,
  .frise20 {
    left: -1500px;
  }
  .frise15,
  .frise16,
  .frise17,
  .frise18,
  .frise19,
  .frise20 {
    padding-top: 35px;
    overflow: hidden;
    z-index: 1;
    position: absolute;
    margin: 4% 0 0 0;
    width: 200% !important;
    height: 150px !important;
    opacity: 1 !important;
    filter: drop-shadow(5px 5px 5px #0000008e);
    transition: 1s;
  }
  .frise16 {
    margin: 10% 0 0 0;
  }
  .frise17 {
    margin: 18% 0 0 0;
  }
  .frise18 {
    margin: 26% 0 0 0;
  }
  .frise19 {
    margin: 34% 0 0 0;
  }
  .frise20 {
    margin: 42% 0 0 0;
  }
}

